/**
 * @ngdoc service
 * @name hsData.hsDataLoader
 * @requires hsData.object.HsData
 * @description
 * Provides a function that loads csv files from a server. Multiple requests to the same address will 
 * be resolved via a caching a single call to the address.
 */
if (typeof(angular) !== 'undefined') { angular.module('hsData').factory('hsDataLoader', function HsDataLoader() { return hs('hsDataLoader'); }); }
 
hs.register('hsDataLoader', ['HsPromise', function HsDataFactory(HsPromise) {
	"use strict";
	
	var urlCache = {};	// caches <url>:HsData pairs
	
	function resolveRelay(url) {
        var i = url.indexOf('?relay=');
        return (i > 0)? '?relay='+encodeURIComponent(url.substring(i+7)) : url;
	}
	
	/** 
     * @ngdoc method
     * @name .#load
     * @methodOf hsData.hsDataLoader
	 * @param {string} csvURL the url to load from
	 * @param {object} http the http obect to use 
	 * @description loads a CSV formatted string from `csvURL` and returns a promise for an 
	 * HsData object initialized with the data. `load` maintians a cache that is checked 
	 * for a prior request to url and creates a new request if none exists.
	 * @return {HsPromise} a promise on an {values, meta} object
	 */
	function load(csvURL, http) {
		if (urlCache[csvURL] === undefined) { 
			urlCache[csvURL] = new HsPromise(function(fulfill, reject) {
				// check for undefined or empty url
				if (!csvURL.length) { reject("invalid url provided"); }
				
				// replace by possible relay, if any
	            var address = resolveRelay(csvURL);
	            
	            // request data
	            http.get(address).success(function receive(data) {
	                // data received successfully
	                fulfill(arrayFromCSVData(data, address));                   
	            }).error(function error(data, status) {
	            	reject('no csvFileName; error code: ' + JSON.stringify(status) + ' for ' + csvURL);
	            });
			}); 
		}
		return urlCache[csvURL];
	}
     
    /**
     * @ngdoc method
     * @name .#arrayFromCSVData
     * @methodOf hsData.hsDataLoader
     * @deprecated
     * @param {String} data the serialized csv-style data in a string
     * @return {object} an object containing a values and a meta array.
     * @description **deprecated as public method** 
     * constructs a {values, meta} object from a table of data in CSV format.
     * 
     */
    function arrayFromCSVData(data) {      
		function addSeries(meta, newName) {
		    if (newName === (parseInt(newName)+'')) { throw 'newName ' + newName + ' can\'t be an index'; }
		    meta[newName] = { name: newName, column: meta.length };    // access column index by name:
		    meta.push(meta[newName]);                                       // access name by column index
		    return meta.length-1;
		}
        // eliminate line feeds inside quotes (Excel export generates these if a cell spans several lines)
        data = data.replace(/(\"[^\"]*?)([\n\r])([^\"]*\")/g, /$1 $3/); // " close the regex quotation
        var lf = (data.match(/\n/g)||[]).length;
        var cr = (data.match(/\r/g)||[]).length;
        var c, r, cols, rows;
        if (cr>2*lf)      { rows = data.split(/[\r]/); }
        else if (lf>2*cr) { rows = data.split(/[\n]/); }
                     else { rows = data.split(/[\r\n]+/); }

        // create names and values struct
        var series = { values:[], meta:[] };
        var exclude = [];                               // columns to exclude if they have no name in first row
        for (r=0; r<rows.length; r++) {
            cols = rows[r].split(',');
            if (r===0) {                                // first row contains headers
                for (c=0; c<cols.length; c++) { 
                    if (cols[c] === '') { exclude.push(c); }
                    else  { addSeries(series.meta, cols[c].trim()); } 
                }
            } else if (rows[r].length >= cols.length) { // rows[r] contains useful info beyond just commas
                for (c=0; c<cols.length; c++) { cols[c] = cols[c].trim(); }
                // remove any excluded columns:
                for (var e=exclude.length-1; e>=0; e--) { cols.splice(exclude[e], 1); }
                series.values.push(cols);               // row = array of colVals
            }
        }
        return series;
    }
       
     /**
     * @ngdoc method
     * @name .#toCSVData
     * @methodOf hsData.hsDataLoader
     * @param {HsData} hsData the data model to be serialized into a CSV string
     * @return {String} an string 
     * @description constructs a HsData object from a table of data in CSV format
     */
    function toCSVData(hsData) {   
    	var names = hsData.colName();  	// array of column names
    	var data = hsData.query().colValues(); // 2D array of values
    	var result = '';
    	var r,c;
    	for (c=0; c<names.length; c++) { result += (c>0?',':'') + names[c]; }
    	result += '\n';
    	for (r=0; r<data.length; r++) {
    		for (c=0; c<data[r].length; c++) { result += (c>0?',':'') + data[r][c]; }
        	result += '\n';
    	}
    	return result;
    }
    
    
	return { 
	    load: 	 	 		load,
		arrayFromCSVData: 	arrayFromCSVData,
	    toCSVData:	 		toCSVData
	};
}]);


