/**
 * @ngdoc service
 * @name hsData.hsDataType
 * @description
 * Provides an object with convenience functions to access in-memory data sets.
 * - {@link hsData.hsDataType#methods_todate hsDataType.toDate} 
 * - {@link hsData.object.HsData#methods_colvalues colValues}
 */

if (typeof(angular) !== 'undefined') { 
	angular.module('hsData').factory('hsDataType', function() { 
		return hs('hsDataType'); 
	}); 
}

hs.register('hsDataType', function() {
    /**
     * @ngdoc method
     * @name .#toDate
     * @methodOf hsData.hsDataType
     * @param {String} str the string to convert to a data
     * @param {integer=} limitYear the year below which the century is corrected. Defaults to 1970.
     * @returns {Date} a new Date object parsed from `str`.
     * @description returns a new Date object parsed from `str` and corrects for a difference in 
     * interpreting centuries between webkit and mozilla in converting strings to Dates:
     * The string "15/7/03" will convert to Jul 15 1903 in Mozilla and July 15 2003 in Webkit.
     * If `limitYear` is not specified this method uses 1970 as the decision date: 
     * years 00-69 will be interpreted as 2000-2069, years 70-99 as 1970-1999.
     */
    function toDate(str, limitYear) {
        limitYear = limitYear || 1970;
        if (!str) { str = '1/1/1970'; }
        var d = new Date(str),
            yy = d.getFullYear();               // to correct for century difference between Webkit and Firefox
        if (yy<limitYear) { d.setFullYear(yy+100); }    // for short years in strings such as '1/1/14'
        return d;
    }

    /**
     * @ngdoc method
     * @name .#castData
     * @methodOf hsData.hsDataType
     * @param {String} type ['date' | 'percent' | 'real' | _any_] The type to cast into. In case of _any_ - i.e. `type` 
     * does not match any of the previous keywords, no casting occurs.
     * @param {float} sample The value to cast.
     * @returns {Object} The result of the cast. 
     * @description Casts the sample to the specified data type.
     */
    function castData(type, sample) {
        var n;
        switch (type) {
            case 'date':    n = toDate(sample);
                            if (isNaN(n.getTime())) { n = null; }
                            break;
            case 'percent': n = parseFloat(sample)/100;
                            if (isNaN(n)) { n = null; }
                            break;
            case 'currency':// replace all except 'e/E', '.', '+/-' and digits
            				if (typeof sample === 'string') { sample = sample.replace(/[^eE\+\-\.\d]/g, ''); }            				
            				/* falls through */
            case 'real':    n = parseFloat(sample); 
                            if (isNaN(n)) { n = null; }
                            break;
            default:        n = ''+sample;
        }
        return n;
     }
     
    /**
     * @ngdoc method
     * @name .#getType
     * @methodOf hsData.hsDataType
     * @param {Object} sample .
     * @returns {String} the type ('real', 'date', 'percent', 'nominal') corresponding to the sample
     * @description determines the data type. Supported types are <pre>
     * 'date':    sample represents a Date, either as a Date object or a String 
     * 'real':    sample represents a real number
     * 'percent': sample represents a percentage (special case of a real number)
     * 'nominal': sample represents a nominal (ordinal or categorical) value
     * </pre>
     */
    function getType(sample) {
        if (sample!=="" && sample) {
            if (sample instanceof Date) { return 'date'; }  // if sample is already a date:
            if (!isNaN(+sample))         { return 'real'; }
            if (sample.indexOf('$')===0) {
            	if (!isNaN(parseFloat(sample.slice(1)))) { return 'currency'; }
            }

            var d = toDate(sample);
            if (d instanceof Date) { // sees ints as valid dates
                if (!isNaN(d)) {
                    if (d.getFullYear()>1900)	{ return 'date'; }
                } 
            }

            if (typeof sample === 'string') { 
                if (sample.indexOf('%') === sample.length-1) {
                    if (!isNaN(parseFloat(sample))) { return 'percent'; }
                }
                if (sample.match(/^\$\d{0,2}((,\d\d\d)*)/g)) {
                    if (!isNaN(parseFloat(sample.trim().replace(/[^eE\+\-\.\d]/g, '').replace(/,/g, '')))) { return 'currency'; }
                }
                switch (sample.toLowerCase()) {
                    case "null": break;
                    case "#ref!": break;
                    default: if (sample.length>0) { return 'nominal'; }
                }
            }
        }
        return null;
    }    
    return {
        toDate:     toDate,
        castData:   castData,
        getType:    getType
    };
});
