/**
 * @ngdoc overview
 * @name index
 * @description
 * 
 * hsData
 * ======
 * Provides utility functions to manage and manipulate in-memory data sets.
 * Functions are provided as hs modules to provide a simple dependency injection mechanism.
 * As a convenience for integration with AngularJS, AngularJS modules are provided as well. 
 * 
 * The codebase is available at {@link https://github.com/HelpfulScripts/hsData}
 * 
 * Overview
 * --------
 * - {@link hsData.hsDataLoader hsDataLoader} provides methods for loading CSV data from a file or a server.
 * - {@link hsData.hsDataType hsDataType} provides methods to determine the data type and type-cast data.
 * - {@link hsData.object.HsData HsData} a simple in-memory database with query, sort and filter functions.
 * 
 * Dependencies
 * ------------
 * - {@link https://github.com/angular/angular.js AngularJS} 1.3.5 or higher; 
 *    optional, for integration with angular modules
 * - {@link #/hs/ hs} 1.0.0 or higher; 
 *    Helpful Scripts foundational services 
 *    
 */
