/**
 * @ngdoc overview
 * @name hsData
 * @description
 * 
 * {@link hsData.object.HsData HsData}
 * =============
 * Provides convenience functions to access in-memory data sets.
 */

	
/**
 * @ngdoc object
 * @name hsData.object.HsData
 * @requires hs.object.HsObject
 * @requires hsData.hsDataType
 * @example
<example module="hsData">
  <file name="index.html">
     <div ng-controller="myDataCtrl">
     <h1>Data</h1>
     	<table>
     	    <th>Row</th><th ng-repeat="h in header">{{h}}</th>
  	 	 	<tr ng-repeat="row in data">
  	 	       <td>{{$index}}</td><td ng-repeat="field in row">{{field}}</td>
  	 	    </tr>
  	 	</table>
  	 	db('Name') = {{db('Name')}}<br>
  	 	db.sort('Name', 'ascending')('Name') = {{db.sort('Name', 'ascending')('Name')}}<br>
  	 	db('Value') = {{db('Value')}}
     </div>
  </file>
  <file name="script.js">
     angular.module('hsData').controller('myDataCtrl', function myCtrl($scope){
        $scope.header = ['Name', 'Value', 'Entry Date', 'Exit Date'];
        $scope.data = [
            ['Harry', '100', '3/1/14', '11/20/14'], 
            ['Mary', '1500', '7/1/14', '9/30/14'],
            ['Peter', '400', '5/20/14', '4/30/15'],  
            ['Jane', '700', '11/13/14', '8/15/15']
          ];
        $scope.db = (new hs('HsData'))('local data');
        $scope.db.data($scope.data, $scope.header);
     });
 </file>
</example>
 * @description
 * Provides an object with convenience functions to access in-memory data sets. Internally, 
 * the data is stored in a simple row-based model using a 2D array [row][column]. 
 * 
 * Column values can be accessed by calling the outer function on the HsData object, 
 * see {@link hsData.object.HsData#example_data_demo example below}.
 * 
 * Columns carry the following meta information: <pre>
 * gMeta[c] = {
 *    name:     string  unique identifier for each column
 *    column:   int     the column index
 *    cast:     boolean whether the colNumber data has been cast yet or not
 *    accessed: boolean whether the column has been accessed (and hence should be cast)
 *    types:    array   describing the possible field types for the column
 *       [{
 *       type:  string  either 'date', 'real', 'percent', or 'nominal'
 *       count: number of fields in column for which type is plausible
 *       domain:range of field values for the type
 *       }]
 * }
 * </pre>
 ## Information Functions
<table>
<tr><td>{@link hsData.object.HsData#methods_colvalues colValues(col)}</td><td>returns the array of column data in the series;</td></tr>
<tr><td>{@link hsData.object.HsData#methods_colnumber colNumber(col)}</td><td>returns the column index of the specified column.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_copy copy()}</td><td>returns a new HsData object representing a copy of the current object.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_coldomain colDomain(col)}</td><td>returns the data domain for the column.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_colname colName()}</td><td>returns all column names.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_colname colName(col)}</td><td>returns the specified column name.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_coltype colType(col)}</td><td>returns the data type for the column.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_query query(conditions)}</td><td>returns an array of rows satisfying each of the conditions.</td></tr>
</table>
 
 ## Functions that change internal state
<table>
<tr><td>{@link hsData.object.HsData#methods_cast cast()}</td><td>casts all columns of values to their predominant type.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_cast cast(col)}</td><td>casts the specified column of values to their predominant type.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_cast cast(type)}</td><td>casts all columns of values to the specified type.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_cast cast(col, type)}</td><td>casts the specified column of values to the specified type.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_data data(values, meta)}</td><td>sets the values and meta information for the HsData object.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_data data(values, meta, deep)}</td><td>sets the values and meta information for the HsData object by creating a deep copy.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_coldomain colDomain(col, newDomain)}</td><td>sets the data domain for the column.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_filter filter(filterObj)}</td><td>Filters the rows of values in array or query mode.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_filter filter(col, filterObj)}</td><td>Filters the rows of values in column mode.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_load load(http)}</td><td>Loads the HsData object from a url or file</td></tr>
<tr><td>{@link hsData.object.HsData#methods_map map(mapFn)}</td><td>Maps the rows of values in array mode.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_map map(col, mapFn)}</td><td>Maps the rows of values in column mode.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_sort sort(sortFn)}</td><td>Sorts the rows of values in array mode.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_sort sort(col, sortFn)}</td><td>Sorts the rows of values in column mode.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_newcolumn newColumn(newName, newFn)}</td><td>Adds a new column of data.</td></tr>
<tr><td>{@link hsData.object.HsData#methods_coltype colType(col, newType)}</td><td>sets the data type for the column.</td></tr>
</table>
 */

if (typeof(angular) !== 'undefined') { 
	angular.module('hsData').factory('HsData', function HsData() { 
		return hs('HsData'); 
	}); 
}

hs.register('HsData', ['HsObject', 'hsDataType', 'hsDataLoader', 'hsDeprecated', function HsData(HsObject, hsDataType, hsDataLoader, hsDeprecated) {
    "use strict";
    
    var gCount = 0;
    var deprecated = hsDeprecated();

    function addSeries(meta, newName, newIndex) {
        if (newName === (parseInt(newName)&'')) { throw 'newName ' + newName + ' can\'t be an index'; }
        var m = meta[newName];
        if (m === undefined) { 
            m = meta[newName] = {};
            m.name   = newName; 
            m.column = newIndex;
            meta.push(m);           // access name by both column name and index
        }
        m.cast 	   = false;         // has not been cast yet
        m.accessed = false;         // has not been accessed yet
        return meta.length-1;
    }
    
    function createData(address) {         	
        /**
         * returns the domain of `values` in column `col` if interpreted as type `type`.
         * If `cast` is true, the data values are also cast as appropriate for `type`.
         * @param {Array} values row array of column array of values
         * @param {integer|String} col the column number of name to analyse
         * @param {String} type the type designator
         * @param {Boolean} cast if true, also cast the data according to type
         */
        function getDomain(col, cast) {
            var s, r;
            col = colNumber(col); 
            if (col === undefined) { throw 'gMeta[' + col + '] is undefined in url ' + obj.url; }
            var types = getTypes(col);
            if (types.length === 0) {
                console.log('*** no values for getting domain in column ' + col + ' \'' + gMeta[col].name + '\'');
                return [];
            } else if (types[0].domain && types[0].domain.length>0 && types[0].forced) {
                return types[0].domain; // domain was explicitely set
            }
            
            var type = types[0].type;
            var domain = (type==='nominal')? [] : [1e99, -1e99];
            var m = meta(col);
            m.accessed = true;
            if (m.cast === true) { cast = false; }  // only cast once
            if (cast === true) { m.cast = true; }
            switch (type) {
                case 'nominal': 
                    for (r=0; r<gValues.length; r++) {
                        s = ' ' + gValues[r][col] + ' ';
                        if (s && s!=='' && s!=='0' && !domain[s] && (s.length !== undefined) && s.length>0) {
                            domain.push(s);
                            domain[s] = true;
                        }
                    }
                    break;
                case 'null': break;
                default:    //ordinal
                    for (r=0; r<gValues.length; r++) {
                        s = gValues[r][col] = cast? hsDataType.castData(type, gValues[r][col]) : gValues[r][col];
                        if (!isNaN(s)) {
                            domain[0] = Math.min(domain[0], s);
                            domain[1] = Math.max(domain[1], s);
                        }
                    }
            }
            if (type === 'date') {
                if (domain[0] < 1e90 && domain[1] > -1e90) {
                    domain[0] = new Date(domain[0]);
                    domain[1] = new Date(domain[1]);
                } else {
                    domain = [];
                }
            }
            types[0].domain = domain;
            return domain;
        }
    
        /**
         * @param {number|string} col an object of series-names:series-values-array pairs. 
         * @return {Array} an array sorted by frequency of encountered types in column `name` of `data`
         */
        function getTypes(col) {
            var m = meta(col);
            if (m.types && m.types.length>0) { return m.types; }
            var t = { };
            col = m.column;
            m.accessed = true;
            for (var r=0; r<gValues.length; r++) {
                var _t = hsDataType.getType(gValues[r][col]);
                if (_t != null) {
                    if (!t[_t]) { t[_t]=0; }
                    t[_t]++;
                }
            }
            var result = [{type:'real', count:0}];
            for (var c in t) { result.push({type: c, count: t[c]}); }
            result.sort(function(a, b) { 
                if (a.type === 'null' ) { return 1; }
                if (b.type === 'null' ) { return -1; }
                if (a.type==='currency'&&a.count>0) { return -1; }
                if (b.type==='currency'&&b.count>0) { return 1; }
                return b.count - a.count;
            });
            m.types = result;
            return result;
        }
        
        /**
         * @ngdoc function
         * @name newColumn
         * @methodOf hsData.object.HsData
         * @param {string} newName the name of the new column
         * @param {function} newFn a function providing values for the new column. The function
         * will be called once for each row with parameters<pre>
         * rowValues: 	array of column-values in current row
         * i:		the current row index
         * values:		the entire current 2D values array
         * </pre> and should return the value for the new column in that row.
         * @return {integer} the index of the new series
         * @description Adds a new column of data as provided by the `newFn` to the data and defines it as a new series 
         * with the name `newName`. `newFn` is invoked for each row in the series with the series' `rowData` provided 
         * as parameter, and should return the new series column value for that row.
         */
        function newColumn(newName, newFn) {
            var newCol = addSeries(gMeta, newName, gMeta.length);            
            // use a mapping on the provided callback to generate the values for the new series:
            obj._values = gValues = gValues.map(function(cols, i, arr) { 
                var rowValues = cols.slice(0);
                rowValues[newCol] = newFn(rowValues, i, arr); 
                return rowValues;
            });
            return newCol;
        }    
        
        /**
         * @ngdoc function
         * @name newRow
         * @methodOf hsData.object.HsData
         * @param {function} newFn a function providing values for the new row. The function
         * will be called once with the current 2D values array as parameter, and should return 
         * an array of new column values for the row.
         * @return {integer} the number of rows in the array.
         * @description Adds a new row of data as provided by the `newFn` to the data. `newFn` is invoked once with the current 2D values array as parameter, and should return 
         * an array of new column values for the row.
         */
        function newRow(newFn) { 
            var row = newFn(gValues);
            gValues.push(row);
            return gValues.length-1;
        }    
        
        /**
         * @ngdoc function
         * @name copy
         * @methodOf hsData.object.HsData
         * @return {HsData} a new HsData object representing a copy of the current object.
         * The new object will have it own copy of the rows array (as returned by {@link hsData.object.HsData#methods_values `values()`}) 
         * to allow for specific filtering and sorting.
         * However it will share the columns array with other objects to be memory efficient. 
         * I.e. after applying {@link hsData.object.HsData#methods_sort sorting} and/or {@link hsData.object.HsData#methods_filter filtering} to the copy, 
         * the array-of-columns for each row in the copy will exist in some unique row of the original.   
         * @description
         */
        function copy() {
            var newObj = createData(obj.url);
            newObj.data(gValues, gMeta, false);
            return newObj;
        }
            
        /**
         * @ngdoc method
         * @name data
         * @methodOf hsData.object.HsData
         * @description sets the `values` and `meta` information for the HsData object by 
         * creating a deep copy (unless `deep` is false) of the values rows array, 
         * and a deep copy of names in the meta array.
         * @param {number [row] [col]} values a 2D array of values
         * @param {Object[]} meta a column array of meta information in either of two formats:
         * - `meta[{name:<name>, column:<index}]`
         * - `meta[<name>]`
         * @param {boolean=} [deep=true] optional flag for creating a deep copy of the data
         * @return {hsData} the hsData object
         */
        function data(values, meta, deep) {
            if (deep===undefined) { deep = true; }
            if (values === undefined || meta === undefined) {
                console.log('*** \'values\' and \'meta\' arrays expected');
                return;
            }
            if (deep) {
                obj._values = gValues = [];
                for (var r=0; r<values.length; r++) {
                    gValues[r] = [];
                    for (var c=0; c<values[r].length; c++) {
                        gValues[r][c] = values[r][c];
                    }
                }
            } else {
            	obj._values = gValues = values.slice();
           	}
            var arr = true;
            obj._meta = gMeta = [];
            if (typeof meta[0] === 'string') { arr = false; }
            for (var n=0; n<meta.length; n++) { 
            	if (arr) { addSeries(gMeta, meta[n].name, meta[n].column); }
            		else { addSeries(gMeta, meta[n], gMeta.length); }
            } 
			if (values.length>0) {
	            if (values[0].length !== meta.length) {
	                console.log(address + ': meta array of length "' + meta.length + '" does not match number of columns ' + values[0].length);
	                console.log(meta);
	                console.log(values[0]);
			        var err = new Error('');
			        console.log(err.stack);                
	            }
	        }
            invalidateDomains();
            return obj;
        }
        
        /**
         * @ngdoc function
         * @name query
         * @methodOf hsData.object.HsData
         * @param {Object} conditions a set of conditions.
         * @return {HsData} an HsData object representing the rows that match the query. 
         * The HsData object will feature its own column meta information, as well as 
         * a copy of the rows array which allows for `filter` and `sort` operations. 
         * However, the column arrays will be shared with the original HsData object in order to be memory efficient.
         * This means that `map` and `newColumn` operations on the new object will affect the original object or any 
         * object derived via `query`.  
         * @description executes a query on the data. Each row in the data is checked and those for which
         * `conditions` is true are returned as a new HsData object.  
         *    ## General conditions
         *    <pre>conditions = 
         *    indexCond || -> conditions on column index
         *    valueCond || -> conditions on column values
         *    </pre>
         *    ## Index conditions
         *    <pre>indexCond =
         *    colIdx        || -> true if column index matches
         *    [colIdx, ...] || -> true if any of the column indices match
         *    </pre>
         *    ## Value conditions
         *    <pre>valueCond = 
         *    termCond  || -> terminal condition 
         *    recurCond || -> recursive combination of conditions
         *    </pre>
         *    ## Terminal conditions
         *    <pre>termCond =
         *    {colName:colValue}                || -> true if colValue matches 
         *    {colName:[colValue, ...]}         || -> true if any of the colValues match
         *    {colName:function(value, rowValues)} -> true if function returns true 
         *    </pre>
         *    ## Recursive combinations
         *    <pre>recurCond =
         *    {'or': valueCond}  || -> true if any condition is true
         *    {'and': valueCond} || -> true if all conditions are true
         *    {valueCond, ...}   || -> same as {'and': {valueCond, ...}}
         *    {'not': valueCond} || -> true if none of the conditions are true
         *    </pre>
         *      
         * @example
            <example module="hsData">
              <file name="index.html">
                 <div ng-controller="myCtrl">
                 <h1>Data</h1>
                 	<table>
                 	    <th>Row</th><th ng-repeat="h in header">{{h}}</th>
              	 	 	<tr ng-repeat="row in data">
              	 	       <td>{{$index}}</td><td ng-repeat="field in row">{{field}}</td>
              	 	    </tr>
              	 	</table>
                 <h1>Queries:</h1>
                 <table>
                    <th>Description</th><th>Query</th><th>('Name')</th><th>.colValues().length</th>
                    <tr><td>undefined query:</td><td></td><td>{{db.query()('Name')}}</td><td>{{db.query().colValues().length}}</td></tr>
                    <tr><td>empty OR query:</td><td>[]</td><td>{{db.query([])('Name')}}</td><td>{{db.query([]).colValues().length}}</td></tr>
                    <tr><td>empty AND query:</td><td>{}</td><td>{{db.query({})('Name')}}</td><td>{{db.query({}).colValues().length}}</td></tr>
                    <tr><td>2nd row:</td><td>1</td><td>{{db.query(1)('Name')}}</td><td>{{db.query(1).colValues().length}}</td></tr>
                    <tr><td>2nd+<br>3rd row:</td><td>[1,2]</td><td>{{db.query([1,2])('Name')}}</td><td>{{db.query([1,2]).colValues().length}}</td></tr>
                    <tr><td>Simple:</td><td>{Name:'Peter'}</td><td>{{db.query({Name:'Peter'})('Name')}}</td><td>{{db.query({Name:'Peter'}).colValues().length}}</td></tr>
                    <tr><td>Compound OR:</td><td>{Name:['Peter', 'Jane']}</td><td>{{db.query({Name:['Peter', 'Jane']})('Name')}}</td><td>{{db.query({Name:['Peter', 'Jane']}).colValues().length}}</td></tr>
                    <tr><td>AND:</td><td>{Name:'Peter', Value:400}</td><td>{{db.query({Name:'Peter', Value:400})('Name')}}</td><td>{{db.query({Name:'Peter', Value:400}).colValues().length}}</td></tr>
                    <tr><td>OR:</td><td>{or:{Name:'Peter', Value:1500}}</td><td>{{db.query({or:{Name:'Peter', Value:1500} })('Name')}}</td><td>{{db.query({or:{Name:'Peter', Value:1500} }).colValues().length}}</td></tr>
                    <tr><td>AND:</td><td>{and:{Name:'Peter', Value:1500}}</td><td>{{db.query({and:{Name:'Peter', Value:1500} })('Name')}}</td><td>{{db.query({and:{Name:'Peter', Value:1500} }).colValues().length}}</td></tr>
                    <tr><td>AND:</td><td>{and:{Name:'Peter', Value:400}}</td><td>{{db.query({and:{Name:'Peter', Value:400} })('Name')}}</td><td>{{db.query({and:{Name:'Peter', Value:400} }).colValues().length}}</td></tr>
                    <tr><td>NOR:</td><td>{not:{Name:'Peter', Value:400}}</td><td>{{db.query({not:{Name:'Peter', Value:400} })('Name')}}</td><td>{{db.query({not:{Name:'Peter', Value:400} }).colValues().length}}</td></tr>
                 </table>
                 </div>
              </file>
              <file name="script.js">
                 angular.module('hsData').controller('myCtrl', function myCtrl($scope){
                    $scope.header = ['Name', 'Value', 'Entry Date', 'Exit Date'];
                    $scope.data = [
                        ['Harry', '100', '3/1/14', '11/20/14'], 
                        ['Mary', '1500', '7/1/14', '9/30/14'],
                        ['Peter', '400', '5/20/14', '4/30/15'],  
                        ['Jane', '700', '11/13/14', '8/15/15']
                      ];
                    $scope.db = (new hs('HsData'))('local data');
                    $scope.db.data($scope.data, $scope.header);
                 });
             </file>
             <file name="style.css">
                table { width: 100%; }
                th, td { padding-left:20px; }
                tr:nth-child(even) {background: #fff}
             </file>
            </example>     
         */
        function query(conditions) {
            try {
                var rows = [];
                for (var r=0; r<gValues.length; r++) { 
                    if (applyCondition(conditions, r, gValues[r])) { rows.push(gValues[r]); }
                }
                
                var newObj = createData(obj.url);
                newObj.data(rows, gMeta, false);

                return newObj;
            } catch(err) {
                console.log(err);
                console.log(err.stack);
            }
        }  
        
		/**
		 * internal recursive function for applying a compound condition to a row of values.
		 * @return true if row matches the condition
		 */        
        function applyCondition(condition, r, rowValues, and) { 
            var orResult = false;
            var andResult= true;          
            if (condition===undefined) { return true; }
            if (typeof condition === 'number') { return (condition === r); }
            if (typeof condition === 'object') {		
                if (condition.length !== undefined) {	// --> an array of elements
                    for (var n=0; n<condition.length; n++) {
                        if (typeof condition[n] === 'number') { 		 	// row numbers
                        	if (condition[n] === r) { orResult  = true; } 
                        						else { andResult = false; }
                        } else if (typeof condition[n] === 'object') { 	// 
                        	if (applyCondition(condition[n], r, rowValues)) { orResult  = true; } // row number match
    															else { andResult = false; }
                        }
                    }
                } else { 								// not a array --> object
                    if (and === undefined) { and = true; }  // if unspecified, consider condition an AND 
                    var conditionMet = and;
                    for (var name in condition) {
                        if      (name==='or')  { conditionMet = applyCondition(condition.or, r, rowValues, false); }
                        else if (name==='and') { conditionMet = applyCondition(condition.and, r, rowValues, true); }
                        else if (name==='not') { conditionMet = !applyCondition(condition.not, r, rowValues, false); }
                        else {
                            var col = colNumber(name);
                            var val = condition[name];
                            var func = (typeof val === 'function');
                            var arr = ((typeof val === 'object') && (val.length!==undefined));
                            if (isNaN(col)) { 
                                throw new Error("invalid colNumber name '" + name + "' for url " + obj.url);
                            } else if (func) { 
                                conditionMet = val(rowValues[col], rowValues);
                            } else if (arr) {
                                conditionMet = (val.length === 0); // empty arrays meet condition
                                for (var c=0; c<val.length; c++) {
                                    if (rowValues[col] === val[c]) { conditionMet = true; } // query true if at least one c true
                                }
                            } else { // object: all conditions have to be met, unless specified as or
                                conditionMet = (rowValues[col] === val); 
                            }
                        }
                        if (conditionMet) { orResult  = true;  if(!and) { break; }} // exit for name loop
                        			 else { andResult = false; if(and)  { break; }}
                    }
                }    
            }
            return and? andResult : orResult;
        }

        
        /**
         * @ngdoc method
         * @name .#_outerFunction
         * @methodOf hsData.object.HsData
         * @param {[number|string]} args the column for which to return values, 
         * passed as the first element in an array,
         * can be either a column index or a vaid key string.
         * @return {number[ ]} returns the array of column data in the series; 
         * @description Outer function, retrieves data from teh HsData object by returning 
         * the result of colValues(args[0).  Use as follows:<pre>
         * var hsData = HsData(url).load();
         * var array = hsData();           // array[row][col]
         * var carr  = hsData('colName');  // array[row]
         */
        function _outerFunction(args) { 
        	var result = colValues(args[0]);
        	return result; 
        }
        
        /**
         * @ngdoc method
         * @name .#colValues
         * @methodOf hsData.object.HsData
         * @param {number|string=} col will return the data in the specified colNumber. 
         * `col` can be either a column index or a vaid key string.
         * If omitted, the entire rows array will be returned
         * @return {number[ ] | number[ ][ ]} if `col` is specified, returns the array [col] of column data in the series.
         * Otherwise returns the the 2D array [row][col] of data in the database.
         * @description column values accessor. 
         */
        function colValues(col) {
        	if (col === undefined) { return gValues; }
        	else {
                col = colNumber(col);
                meta(col);
                getDomain(col, true);
                var result = [];           
                for (var r=0; r<gValues.length; r++) { result[r] = gValues[r][col]; }
                return result;
        	}
        } 
        
         /**
          * internal function
          */
         function meta(colIndex) {
        	 if (!gMeta[colIndex]) { return undefined; }
        	 gMeta[colIndex].accessed = true;
        	 return gMeta[colIndex];
         } 

        /**
         * @ngdoc function
         * @name colType
         * @methodOf hsData.object.HsData
         * @param {number|string} col the data column (by index or name) for which to get or set the type.
         * @param {string=} newType optional type override.
         * @return {string | HsData} the {@link hsData.hsDataType#methods_gettype data type} for the column if no `newType` is specified,
         * or the data object if a type override was provided to allow for command chaining; 
         */
        function colType(col, newType) {
            var m = meta(col);
            if (!m) { return undefined; }
            if (newType) { 
                var types = getTypes(col);
                types[0].type = newType; 
                types[0].domain = undefined;
                m.cast = false;
                return obj;
            }
            return getTypes(col)[0].type;
        }
        
        /**
         * @ngdoc function
         * @name colDomain
         * @methodOf hsData.object.HsData
         * @param {number|string} col the data column for which to get the type.
         * @param {number[...]=} newDomain optional type override.
         * @return {string} the data domain for the column. For `nominal` types this is an array
         * containing all observed values; for types `real`, `data`, or `percent`, an array with [min, max] is returned.
         */
        function colDomain(col, newDomain) {
            var m = meta(col);
            if (!m) { return undefined; }
            if (newDomain) {
                getTypes(col)[0].domain = newDomain;
                getTypes(col)[0].forced = true;
            }
            return getDomain(col, true);
        }
        
        /**
         * @ngdoc function
         * @name colNumber
         * @methodOf hsData.object.HsData
         * @param {number|string} column the data column for which to return the name. 
         * @description returns the column index of the specified column. 
         * `col` can be either an index or a name.
         * @return {number} the column number or `undefined`.
         */
        function colNumber(col) {
            var m = meta(col);
            if (!m) { return undefined; }
            m.accessed = true; 
            return m.column; 
        }
        
        /**
         * @ngdoc function
         * @name colName
         * @methodOf hsData.object.HsData
         * @param {number|string=} col optional data column for which to return the name. `col` can be either an index or a name.
         * @description returns the specified column name or all column names.
         * @return {string | string[ ]} the name of the specified column, or an array of column names if `col` is omitted,
         * or `undefined`.
         */
        function colName(col)  { 
            if (col === undefined) {
                var names = [];
                for (var c=0; c<gMeta.length; c++) {
                    names[c] = gMeta[c].name;
                }
                return names;
            } else {
                var m = meta(col);
            	if (!m) { return undefined; }
            	m.accessed = true; 
            	return m.name;
            }
        }
        
        function invalidateDomains() {
            for (var c=0; c<gMeta.length; c++) { gMeta[c].domain = undefined; }
        }
        
        /** 
         * @ngdoc function
         * @name filter
         * @methodOf hsData.object.HsData
         * @param {number=} col optional; the data column (index or name) to apply conditions to. 
         * @param {function | conditions} filterObj either a filter function of a conditions object. 
         * It follows the same specifications as 
         * - if a function, the function passed to Array.filter()
         * - if a conditions object, the conditions object passed to query()
         * @return {HsData} the HsData object in order to allow for chaining.
         * @description Filters the rows of values based on the result of the `filterObj`. The method behaves 
         * similarly as the Array.filter() method, except that it filters in place rather than return a new copy of the array.
         * Two modes are supported:
         * # Array Mode (deprecated)
         * If `col` is omitted and `filterObj` is a function, the column arrays for each row are passed as 
         * samples into the `filterObj`. This allows for complex filters combining conditions across multiple columns.<br>
         * `data.filter(function(cols){ return (cols[3]>0) && (cols[5]!==''); });`
         * # Column mode (deprecated)
         * If `col` is specified, either as index or by column name, the respective column value is passed
         * into `filterObj`. This allows filtering for simple conditions.<br>
         * `data.filter('Date', function(col) { return col.getYear() > 2014; });`
         * # Query Mode
         * If none of the above, `filterObj` specifies a set of query conditions as described in {@link hsData.object.HsData#methods_query query}
         */
        function filter(col, filterObj) {
            if (typeof col === 'function') {
//            	deprecated('deprecated call to filter in Column mode');
                filterObj = col;
                obj._values = gValues = gValues.filter(filterObj);
                invalidateDomains();
            } else if(((typeof col === 'string') || (typeof col === 'number')) && (typeof filterObj === 'function')){
            	deprecated('deprecated call to filter in Array mode');
                col = colNumber(col);
                obj._values = gValues = gValues.filter(function(cols) {
                    return filterObj(cols[col]);
                });
            } else {
                filterObj = col;
                var result = [];
                for (var r=0; r<gValues.length; r++) { 
	                if (applyCondition(filterObj, r, gValues[r])) { result.push(gValues[r]); }
	            }
                obj._values = gValues = result;
                invalidateDomains();
            }
            return obj;
        }
        
        /**
         * @ngdoc function
         * @name sort
         * @methodOf hsData.object.HsData
         * @param {number=} col optional; the data column to use for sorting. 
         * @param {function | string} sortFn a function to implement the conditions, 
         * follows the same specifications as the function passed to Array.sort(). 
         * Some predefined sort function can be invoked by providing a 
         * respective string instead of a function. The following functions are defined:
           <table>
         	<tr><td>'`ascending`'</td><td>sort in ascending order.</td></tr>
         	<tr><td>'`descending`'</td><td>sort in decending order.</td></tr>
           </table>
         * @return {HsData} the HsData object in order to allow for chaining.
         * @description Sorts the rows of values based on the result of the `sortFn`, 
         * which behaves similarly to the Array.sort method.  
         * Two modes are supported:
         * # Array Mode
         * If `col` is omitted, the column arrays are passed as samples into the `sortFn`. 
         * This allows for complex sorts, combining conditions across multiple columns.<br>
         * `data.sort(function(row1, row2){ return row1[5] - row2[5]; });`
         * # Column mode
         * If `col` is specified, either as index or by column name, the respective column value is passed
         * into `sortFn`. This allows filtering for simple conditions.<br>
         * **The specified column will be automatically cast prior to sorting**<br>
         * `data.sort('Date', function(val1, val2) { return val1 - val2; });`
         */
        function sort(col, sortFn) {            
        	function descendingNum(a, b) { return b-a; }
        	function ascendingNum(a, b) { return a-b; }
        	function descending(a, b) { return (b>a)?1:((b===a)?0:-1); }
        	function ascending(a, b) { return (a>b)?1:((b===a)?0:-1); }

            if (typeof col === 'function') {
                sortFn = col;
                gValues.sort(sortFn);
            } else {
                cast(col);
                col = colNumber(col);
                var t = colType(col);
                if (typeof sortFn === 'string') {
                    if (sortFn==='descending') { sortFn = (t==='nominal')? descending : descendingNum; }
                    else if (sortFn==='ascending') { sortFn = (t==='nominal')? ascending : ascendingNum; }
                    else {
                        console.log('sort function "' + sortFn + '" not recognized; defaulting to descending sort');
                        sortFn = descending; 
                    }
                }
                gValues.sort(function(row1, row2) { 
                    return sortFn(row1[col], row2[col]); 
                });
            }
            return obj;
        }
        
        /** 
         * @ngdoc function
         * @name map
         * @methodOf hsData.object.HsData
         * @param {number=} col optional; the data column to apply the mapping to. 
         * @param {function | string} mapFn a function to implement the mapping, 
         * follows the same specifications as the function passed to Array.map().<br>
         * For column mode, some predefined map functions can be invoked by providing a 
         * respective string instead of a function. The following functions are defined:
           <table>
         	<tr><td>'cumulate'</td><td>replace value with the cumulative sum of values up to the current element.</td></tr>
           </table>
         * @return {HsData} the HsData object in order to allow for chaining.
         * @description Maps one or more columns in each rows of values based 
         * on the result of the `mapFn`, which behaves similarly to the Array.map() method.
         * Two modes are supported:
         * # Array Mode
         * If `col` is omitted, the `mapFn` is passed the column arrays per row as parameter. 
         * This allows for complex mapping combining conditions across multiple columns.<pre>
         * data.map(function(values){ 
         *    values[1] = values[3] * values[5]; 
         *    return values; 
         * });</pre>
         * Be sure to return the `values` array as a result.
         * # Column mode
         * If `col` is specified, either as index or by column name, the respective column value is passed
         * into `mapFn`, along with the row index and the entire row array. This allows for simple mapping.<pre>
         * data.map('Price', function(value, i, values) { 
         *    return value * 2; 
         * });</pre>
         */
        function map(col, mapFn) {
            function noop(val) { return val; }
            function cumulate() { var sum=0; return function(val) { sum += +val; return sum; };}
            
            if (typeof col === 'function') {
                mapFn = col;
                obj._values = gValues = gValues.map(mapFn);
                invalidateDomains();
            } else {
                col = colNumber(col);
                if (mapFn==='cumulate') { mapFn = cumulate(); }
                else if (mapFn==='noop') { mapFn = noop; }
                else if (typeof mapFn !== 'function') { 
                	console.log('map function "' + mapFn + '" not recognized; defaulting to noop function');
                	mapFn = noop;  
                }

                obj._values = gValues = gValues.map(function(cols, i/*, arr*/) { 
                    var newCols = cols.slice(0);
                    newCols[col] = mapFn(newCols[col], i, newCols); 
                    return newCols;
                });
                gMeta[col].domain = undefined; 
            }
            return obj;
        }
        
        /**
         * @ngdoc method
         * @name .#cast
         * @methodOf hsData.object.HsData
         * @param {number|array=} col the column, or array of columns, to cast. Indeces and names are valid.
         * If omitted, all previously accessed columns will be cast.
         * @param {string=} type the type to cast to. This overrides the automatic type detection.
         * Only valid if col is specified.
         * {@link hsData.hsDataType#castData hsDataType.castData} for valid type specifiers.
         * @description casts columns of values to their predominant type. 
         */
        function cast(col, type) {
            var c;
            function castCol(col) {
                if (type !== undefined) { getTypes(col)[0].type=type; }
                meta(colNumber(col)).cast = false;
                getDomain(col, true);
            }
            if (col === undefined) {
                for (c=0; c<gMeta.length; c++) {
                    gMeta[c].cast = false;
                    getDomain(c, true);
                }
            } else {
                if (col instanceof Array) {
                    for (c=0; c<col.length; c++) { castCol(col[c]); }
                } else { 
                    castCol(col); 
                }
            }
        }
        
	    /**
	     * @ngdoc function
	     * @name load
	     * @methodOf hsData.object.HsData
	     * @param {object=} http an optional http access object to use. Supported objects are
	     * - angular $http
	     * - jquery $
	     * - or any object that supports the pattern `*.get(address).success(callback).error(callback)`
	     * @description a static load function. Loads a CSV formatted string from `csvURL` and returns a promise for an 
	     * HsData object initialized with the data. If multiple HsData objects call `load` with the same url,
	     * each HsData object will contain a shallow copy of the original data.  
	     * @return {HsPromise} a promise on an initialized HsData object.
	     */
        function load(http) {
			return hsDataLoader.load(obj.url, http).then(
				function success(series) { 
					return obj.data(series.values, series.meta, false);
				},
				function failure(reason) { 
					return console.log("Error loading data from " + obj.url + ":" + reason);
				}
			);
	     }
        
        var gValues;	// 2D array [row][column]
        var gMeta; 
        var obj = new HsObject('HsData');
        obj._outerFunction = _outerFunction;
        /**
         * @ngdoc object
         * @name url
         * @propertyOf hsData.object.HsData
         * @description the URL from where the data originated
         */
        obj.url         = address;
        obj.copy        = copy;     
        obj.data        = data;     
        obj.load        = load;     
        obj.newColumn   = newColumn;
        obj.newRow	    = newRow;
        obj.query       = query;    
        obj.colType     = colType;     
        obj.colDomain   = colDomain;   
        obj.colValues	= colValues;
        obj.colNumber   = colNumber;   
        obj.colName     = colName;     
        obj.filter      = filter;   
        obj.sort        = sort;     
        obj.map         = map;      
        obj.cast        = cast;     
        obj.id          = parseInt(Math.random()*100);
        gCount++;
//        console.log('data object ' + obj.id + ', total: ' + gCount);
        return obj;
    }
     
    return createData;
}]);
